import Loadable from 'react-loadable';
import PropTypes from "prop-types";
import {Dialog, Tabs, Tab} from "@blueprintjs/core";
import React from "react";

const NewCenter = Loadable({
    loader: () => import('./NewCenter'),
    loading: () => null,
});

const NewMultipleCenters = Loadable({
    loader: () => import('./NewMultipleCenters'),
    loading: () => null,
});

// const EditCenter = Loadable({
//     loader: () => import('./EditCenter'),
//     loading: () => null,
// });

export class NewCenterDialog extends React.Component {

    state = {
        tabId: 0
    }

    render(){
        const {history} = this.props;

        return (
            <Dialog
                backdropClassName="transparent__back"
                style={{width: '350px'}}
                lazy
                onClose={() => history.goBack()}
                hasBackdrop={false}
                canOutsideClickClose={false}
                icon="marker"
                isOpen title="New center(s)"
            >
                <div className="bp3-dialog-body">
                    <Tabs   
                        selectedTabId={this.state.tabId}
                        onChange={(nextTabId, prevTabId) => this.setState({tabId: nextTabId})}
                        large={false}
                        renderActiveTabPanelOnly id={"newCenterTabs"}>

                        <Tab id={0}
                             title="Single" panel={<NewCenter />} />

                        <Tab id={1}
                             title="Multiple" panel={<NewMultipleCenters />} />

                        <Tabs.Expander />

                    </Tabs>
                </div>
            </Dialog>
        );
    }

    static propTypes = {
        history: PropTypes.object.isRequired
    };
};

